import React, { Component } from 'react';
import { H1, H2, H5, Subtitle, NextBtn, Highlights, Tag } from '../css/fonts.js';
import { InnerContainer, Br, TwoCol, Bg, TagContainer } from '../css/container.js';
import { sweetContent } from '../content.js';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import Image from '../components/Image';

class Sweet extends Component {
  render() {
    return (
      <div>
        <Navbar/>
        <InnerContainer>
          <br/><br/><br/><br/>
          <Br/><Br/>
          <H1 large center>{sweetContent.title}</H1>
          <TagContainer>
            <Tag>Internship</Tag>
            <Tag>Apr 2020 - Sep 2020</Tag>
          </TagContainer>

          <br/><br/><Br/>
          <TwoCol even>
            <div>
              <Subtitle>CONTEXT</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.context }}/>
            </div>
            <div>
              <Subtitle>MY ROLE</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.role }}/>
            </div>
          </TwoCol>
        </InnerContainer>
        <br/>
        <Bg>
        <InnerContainer>
          <br/><br/>
          <TwoCol even>
            <div>
              <Subtitle>PROBLEM</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.problem }}/>
            </div>
            <div>
              <Subtitle>CHALLENGE</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.challenge }}/>
            </div>
          </TwoCol>
        </InnerContainer>
        <br/>
      </Bg>
      <InnerContainer>
          <br/><br/>
          <Subtitle>SET RESEARCH GOAL</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: sweetContent.motivation }}/>
          <br/><br/>
          <H2 orange>Survey to get team on board  User Interview to dig deeper</H2>
          <Subtitle black>High-leve process</Subtitle>          
          <img src={sweetContent.processimg} alt={sweetContent.processimg} style={{"width":"100%","margin":"auto"}}/>
          <br/><br/><br/>
          <TwoCol even>
            <div>
              <Subtitle black dangerouslySetInnerHTML={{ __html: sweetContent.surveytitle }}/>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.survey }}/><br/>
              <Subtitle black>Findings</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.surveyresult }}/><br/>
              <Subtitle black>Limitations</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.surveylimitation }}/>
            </div>
            <div>
              <Subtitle center black thin>Research Questions</Subtitle>
              <Highlights left dangerouslySetInnerHTML={{ __html: sweetContent.surveyquestion }}/>
            </div>
          </TwoCol>
          <br/><br/>
          <TwoCol even>
            <div>
              <Subtitle black dangerouslySetInnerHTML={{ __html: sweetContent.interviewtitle }}/>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.interview }}/><br/>
              <Subtitle black>Findings</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.interviewresult }}/><br/>
              <Subtitle black>Limitations</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.interviewlimitation }} /><br/>
            </div>
            <div>
              <Subtitle center black thin>Refine research goal</Subtitle>
              <Highlights left dangerouslySetInnerHTML={{ __html: sweetContent.interviewgoal }}/>
                <Subtitle center black thin>Guiding questions</Subtitle>
                <Highlights left dangerouslySetInnerHTML={{ __html: sweetContent.interviewquestions }}/>
            </div>
          </TwoCol>
          </InnerContainer>
          <Bg>
            <br/><br/>
          <InnerContainer>
            <Subtitle>IMPACTS</Subtitle>
            <H5 dangerouslySetInnerHTML={{ __html: sweetContent.impact }} />
            <br/>
              <Subtitle>TAKEAWAYS</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: sweetContent.takeaways }} />
            <br/>
      </InnerContainer>
      </Bg>
      <NextBtn to={sweetContent.link}>
        <div dangerouslySetInnerHTML={{ __html: sweetContent.action }} />
      </NextBtn>
      <br/><br/><br/><br/>
      <Footer/>
      </div>
    );
  }
}

export default Sweet;
